                    Button(v-if="agreement.objects.length !== 0" variety="primary-light" :to="`/agreements/${agreement.id}/allcounters`")  
                    //- Button(variety="primary-light" :to="`/agreements/${agreement.id}/${agreement.id}/bills`" v-if="!isMobile") 
                    Button(variety="primary-light" :to="`/agreements/${agreement.id}/bills`" v-if="!isMobile") 
                    
                    Button(variety="primary-light-outline" v-if="!isMobile") 
