        //-             Button(variety="primary-light" :to="`/agreements/${agreement.id}/bills`" v-if="!isMobile") 
                    
        //-             Button(variety="primary-light-outline" v-if="!isMobile")     
        //- .error(v-else)
        //-     h1 {{ error }}            
    LayoutSidebar
        router-link(to="/support/")  
        SidebarSupport(:items="tickets")
</template>

<script>
import {
    Card,
    LayoutSidebar,
    SidebarSupport,
    Button,
    Loading
} from '@/components'
import Vue from 'vue'

export default {
    name: 'AgreementsList',
    data () {
        return {
            data: [
                {
                    name: ' 101016437',
                    client: '  ..',
                    type: '5-1234'
                },
            ],
            
            openedMenu: [],
            error: '',
            active: true,
        }
    },
    methods: {
        // activeHandler() {
        //     this.$data.active = !this.$data.active
        // },

        formatNumber(num){
            const number = (num < 0) ? num * -1 : num;
            return number;
        }
    },
    components: { Card, LayoutSidebar, SidebarSupport, Button, Loading },
    computed: {
        isMobile () {
            return screen.width < 760
        },
        profileData() {
            return JSON.parse(Vue.cookie.get('profileData'))
        },
        name() {
            return Vue.cookie.get('contrName')
        },
        getBills () {
            try {
                // console.log('test', this.$store.getters.getAgreements)
                return this.$store.getters.getAgreements.filter((el)=>el.active ===true)
            } catch {
                this.error = '    '
            }
        },
        // getUnactiveBills () {
        //      try {
        //         return this.$store.getters.getAgreements.filter((el)=>el.active === false);
        //     } catch {
        //         this.error = '    '
        //     }
        // },
        is_loading() {
            return this.$store.getters.is_loading
        },
        tickets() {
            return this.$store.getters["supportModule/tickets"];
        },
    },
    async mounted(){
        await this.$store.dispatch("supportModule/init");
        this.$data.data = this.getBills.filter((el)=>el.active ===true)
    }
}
</script>

<style lang="sass">
@import @/assets/styles/vars

.agreements-list
    display: flex
    flex-direction: row
    width: 100%

    .layout__tabs
        line-height: 1.15
        box-sizing: border-box
        display: flex
        padding: 0 0.5rem

    .layout__tabs-item_active
        border-bottom: 1px #3F64A9
        color: #3F64A9 !important

    .layout__tabs-item
        box-sizing: border-box
        line-height: 1.5rem
        margin: 0 1rem
        display: flex
        padding: 1.125rem 0 calc(1.125rem - 2px)
        color: #7F8DA8
        text-decoration: none
        font-weight: 500

    .layout__main
        flex-grow: 1

</style>
